.avatar {
  align-self: flex-start;
  padding: 10px 20px;
}

.searchBarContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  background-color: whitesmoke;
  border-bottom: 1px solid lightgrey;
}

.searchInput {
  width: 90%;
  height: 30px;
  background-color: white;
  border-radius: 20px;
  border: 1px solid lightgrey;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
}

.searchInput > svg {
  padding: 0 10px;
  color: lightgrey;
}

.searchInputText {
  flex: 1;
  border: none;
  outline: none;
  font-size: 13px;
  color: black;
  margin-right: 10px;
}
